import React from 'react';
import { Button } from '@chakra-ui/core';

const Pagination = ({ next, prev, onNext, onPrev }) => (
  <div>
    {prev && <Button onClick={onPrev}>{prev}</Button>}
    {prev ? prev + 1 : next - 1}
    {next && <Button onClick={onNext}>{next}</Button>}
  </div>
);

export default Pagination;
